import React, { Fragment } from "react";

// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Slide from "@material-ui/core/Slide";
import IconButton from "@material-ui/core/IconButton";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "components/CustomButtons/Button.jsx";

import Parallax from "components/Parallax/Parallax.jsx";
import HonorBg from "assets/img/honor-bg.jpg";
import Close from "@material-ui/icons/Close";

import "./HonorPage.css";
import timeline from "./Timeline";

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

class HonorPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      classicModal: false,
      modalImgSource: ""
    };
  }
  handleClickOpen(modal) {
    const x = [];
x[modal] = true;
    this.setState(x);
    console.log({modal})
  }
  handleClose(modal) {
    const x = [];
    x[modal] = false;
    this.setState(x);
  }
  handleModalImg(link){
    this.setState({ modalImgSource: link });
    this.handleClickOpen("classicModal");
  }
  render() {
    const { ...rest } = this.props;
    return (
      <Fragment>
        <Header
          color="transparent"
          brand="NTOU 資料庫實驗室"
          rightLinks={<HeaderLinks />}
          fixed
          changeColorOnScroll={{
            height: 200,
            color: "white"
          }}
          {...rest}
        />
        <Parallax small filter image={HonorBg} />
        <Dialog
          classes={{
            root: "center",
            paper: "modal"
          }}
          open={this.state.classicModal}
          TransitionComponent={Transition}
          keepMounted
          onClose={() => this.handleClose("classicModal")}
          aria-labelledby="classic-modal-slide-title"
          aria-describedby="classic-modal-slide-description"
        >
          <DialogTitle
            id="classic-modal-slide-title"
            disableTypography
            className="modalHeader"
          >
            <IconButton
              className="modalCloseButton"
              key="close"
              aria-label="Close"
              color="inherit"
              onClick={() => this.handleClose("classicModal")}
            >
              <Close className="modalClose" />
            </IconButton>
          </DialogTitle>
          <DialogContent
            id="classic-modal-slide-description"
            className="modalBody"
          >
            {this.state.modalImgSource ? (
              <img
                src={this.state.modalImgSource}
                className="modalImg"
                alt="獎狀證明"
              />
            ) : (
              <img
                src={require("assets/img/honor/default.jpg")}
                className="modalImg"
                alt="獎狀證明"
              />
            )}
          </DialogContent>
          <DialogActions className="modalFooter">
            <Button
              onClick={() => this.handleClose("classicModal")}
              color="danger"
              simple
            >
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <div className="bg-wrapper">
          <div className="container">
            <h3 className="header-three">榮譽榜</h3>
            <GridContainer>
              <GridItem xs={12} sm={12} md={12}>
                <div className="timeline">
                  {timeline.map((item, i) => (
                    <Fragment key={i}>
                      {i % 2 === 0 ? (
                        <div className="timeline-wrapper left">
                          <div className="content">
                            <h3 className="timeline-year">{item.date}</h3>
                            <div className="timeline-organizer">
                              <span className="timeline-label">主辦單位:</span>{" "}
                              {item.organizer}
                            </div>
                            <div className="timeline-list">{item.list}</div>
                            <div className="timeline-winner">
                              <span className="timeline-label">得獎者:</span>{" "}
                              {item.winner}
                            </div>
                            <div className="timeline-btn-pdf">
                              <Button
                                onClick={() =>{this.handleModalImg(item.link)}}
                                className="btn btn-primary"
                              >
                                View
                              </Button>
                            </div>
                          </div>
                        </div>
                      ) : (
                        <div className="timeline-wrapper right">
                          <div className="content">
                            <h3 className="timeline-year">{item.date}</h3>
                            <div className="timeline-organizer">
                              <span className="timeline-label">主辦單位:</span>{" "}
                              {item.organizer}
                            </div>
                            <div className="timeline-list">{item.list}</div>
                            <div className="timeline-winner">
                              <span className="timeline-label">得獎者:</span>{" "}
                              {item.winner}
                            </div>
                            <div className="timeline-btn-pdf">
                              <Button
                                onClick={() =>{this.handleModalImg(item.link)}}
                                className="btn btn-primary"
                              >
                                View
                              </Button>
                            </div>
                          </div>
                        </div>
                      )}
                    </Fragment>
                  ))}
                </div>
              </GridItem>
            </GridContainer>
          </div>
        </div>
        <Footer />
      </Fragment>
    );
  }
}

export default HonorPage;
