import React, { Fragment } from "react";

// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";

import HeaderLinks from "components/Header/HeaderLinks.jsx";

import Parallax from "components/Parallax/Parallax.jsx";

import LinksBg from "assets/img/link_bg.jpg";

import SectionTabs from "views/Components/Sections/SectionTabs.jsx";

import './LinksPage.css'

class HonorPage extends React.Component {
  render() {
    const { ...rest } = this.props;
    return (
      <Fragment>
        <Header
          color="transparent"
          brand="NTOU 資料庫實驗室"
          rightLinks={<HeaderLinks />}
          fixed
          changeColorOnScroll={{
            height: 80,
            color: "white"
          }}
          {...rest}
        />
        <Parallax small filter image={LinksBg} />
        <div className="bg-wrapper">
          <div className="container">
            <SectionTabs />
          </div>
        </div>
        <Footer />
      </Fragment>
    );
  }
}

export default HonorPage;
