import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// @material-ui/icons
import HistoryIcon from "@material-ui/icons/History";
import TrendingIcon from "@material-ui/icons/TrendingUp";
import StarIcon from "@material-ui/icons/Star";
import LinkIcon from "@material-ui/icons/InsertLink";
import WhatShotIcon from "@material-ui/icons/Whatshot";
// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import InfoArea from "components/InfoArea/InfoArea.jsx";

import productStyle from "assets/jss/material-kit-react/views/landingPageSections/productStyle.jsx";

import Info from "components/Typography/Info.jsx";
import Success from "components/Typography/Success.jsx";
import previousProjects from "./PreviousProjects";
import recentlyProjects from "./RecentlyProjects";

import Link from "@material-ui/core/Link";

class ProductSection extends React.Component {
  render() {
    const { classes } = this.props;
    const previousProjectsComponent = previousProjects.map(project =>{
      return (
        <Info key={project.id}>
          {project.link ? (
            <div>
              <LinkIcon
                style={{
                  verticalAlign: "middle",
                  marginRight: 5,
                  marginBottom: 10
                }}
              />
              <Link
                href={project.link}
                target="_blank"
                rel="noopener noreferrer"
                color="inherit"
              >
                {project.name}
              </Link>
            </div>
          ) : (
            <div>
              <StarIcon
                style={{
                  verticalAlign: "middle",
                  marginRight: 5,
                  marginBottom: 10
                }}
              />
              {project.name}
            </div>
          )}
        </Info>
      )
    })
    const recentlyProjectsComponent = recentlyProjects.map(project =>{
      return (
        <Success key={project.id}>
          {project.link ? (
            <div>
              <LinkIcon
                style={{
                  verticalAlign: "middle",
                  marginRight: 5,
                  marginBottom: 10
                }}
              />
              <Link
                href={project.link}
                target="_blank"
                rel="noopener noreferrer"
                color="inherit"
              >
                {project.name}
              </Link>
            </div>
          ) : (
            <div>
              <WhatShotIcon
                style={{
                  verticalAlign: "middle",
                  marginRight: 5,
                  marginBottom: 10
                }}
              />
              {project.name}
            </div>
          )}
        </Success>
      )
    })
    return (
      <div className={classes.section}>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={8}>
            <h2 className={classes.title}>關於我們</h2>
            <h5 className={classes.description} style={{textAlign : 'left'}}>
              因應資訊化時代的成長，資料量增加，在西元1997年國立台灣海洋大學資訊科學系成立時，資料庫實驗室也伴隨著其成長。<br/><br/>
              本實驗室在張雅惠教授的領導下，朝向XML、Database System、Internet Application和地理資訊系統等領域發展。主要著重於資料處理相關技術，發展更快速的文件查詢演算法和資 料結構，讓資料處理效率和文件傳輸速度加倍。
            </h5>
          </GridItem>
        </GridContainer>
        <div>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <InfoArea
                title="實驗室過去執行的計畫"
                description=""
                icon={HistoryIcon}
                iconColor="info"
                vertical
              />
              {previousProjectsComponent}
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <InfoArea
                title="近幾年執行的計畫"
                description=""
                icon={TrendingIcon}
                iconColor="success"
                vertical
              />
              {recentlyProjectsComponent}
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(productStyle)(ProductSection);
