import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";

import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";

import Parallax from "components/Parallax/Parallax.jsx";

import profile from "assets/img/faces/YaHuiChang.jpg";

import PeopleBg from "assets/img/people_bg.jpg";

import profilePageStyle from "assets/jss/material-kit-react/views/profilePage.jsx";

import Primary from "components/Typography/Primary.jsx"
import Muted from "components/Typography/Muted.jsx"

import TeamSection from "../ProfilePage/Sections/TeamSection.jsx"

class ProfilePage extends React.Component {
  render() {
    const { classes, ...rest } = this.props
    const imageClasses = classNames(
      classes.imgRaised,
      classes.imgRoundedCircle,
      classes.imgFluid
    )
    return (
      <div>
        <Header
          color="transparent"
          brand="NTOU 資料庫實驗室"
          rightLinks={<HeaderLinks />}
          fixed
          changeColorOnScroll={{
            height: 200,
            color: "white"
          }}
          {...rest}
        />
        <Parallax small filter image={PeopleBg} />
        <div className={classNames(classes.main, classes.mainRaised)}>
          <div>
            <div className={classes.container}>
              <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={6}>
                  <div className={classes.profile}>
                    <div>
                      <img
                        src={profile}
                        alt="指導教授"
                        className={imageClasses}
                      />
                    </div>
                    <div className={classes.name}>
                      <h3 className={classes.title}>張雅惠 (Ya-Hui Chang)</h3>
                      <h4>職稱：教授</h4>
                      <Primary>Email: yahui@mail.ntou.edu.tw</Primary>
                      <Primary>Office： INS616</Primary>
                    </div>
                  </div>
                </GridItem>
              </GridContainer>
              <div className={classes.description}>
                <Muted>國立台灣大學資訊工程學士</Muted>
                <Muted>美國馬里蘭大學計算機科學碩士</Muted>
                <Muted>美國馬里蘭大學計算機科學博士</Muted>
              </div>
              <div className={classes.container}>
                <TeamSection />
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default withStyles(profilePageStyle)(ProfilePage);
