import React , {Fragment} from 'react'

import Header from "components/Header/Header.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Parallax from 'components/Parallax/Parallax.jsx'
import HonorBg from "assets/img/honor-bg.jpg";

import './ProjectsPage.css'

class ProjectsPage extends React.Component {
    render(){
        const { ...rest } = this.props;
        return(
            <Fragment>
                <Header
                    color="transparent"
                    brand="NTOU 資料庫實驗室"
                    rightLinks={<HeaderLinks />}
                    fixed
                    changeColorOnScroll={{
                        height: 200,
                        color: "white"
                    }}
                    {...rest}
                />
                <Parallax small filter image={HonorBg} />
                <div className="bg-wrapper">
                    <div className="container">
                        <h3 className="header-three">项目网站</h3>
                <div className="project-wrapper mb-20">
                    <a href="http://www.db.cs.ntou.edu.tw/2009%20Driving%20School%20Project/index.aspx" target="_blank">
                    <div className="project-bg"><img src={require('assets/img/img-system001.jpg')} alt="" className="img-fluid"/></div>
                    <div className="project-title">大龍港駕訓班資料系統</div>
                    </a>
                </div>
                        <div className="project-wrapper mb-20">
                            <div className="project-bg"><img src={require('assets/img/img-system002.jpg')} alt="" className="img-fluid"/></div>
                            <div className="project-title">電子病例檢索系統</div>
                        </div>
                        <div className="project-wrapper mb-20">
                            <div className="project-bg"><img src={require('assets/img/img-system003.jpg')} alt="" className="img-fluid"/></div>
                            <div className="project-title">防洪預警系統</div>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default ProjectsPage