/*eslint-disable*/
import React from "react";
// react components for routing our app without refresh
import { Link } from "react-router-dom";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";

// @material-ui/icons
import { Apps, Assignment, People, FolderSpecial, LinkSharp, School, Book } from "@material-ui/icons";

// core components
import CustomDropdown from "components/CustomDropdown/CustomDropdown.jsx";
import Button from "components/CustomButtons/Button.jsx";

import headerLinksStyle from "assets/jss/material-kit-react/components/headerLinksStyle.jsx";

function HeaderLinks({ ...props }) {
  const { classes } = props;
  return (
    <List className={classes.list}>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          buttonText="实验室主机"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={Apps}
          dropdownList={[
            <a
              href="http://140.121.199.211:8080"
              target="_blank"
              className={classes.dropdownLink}
            >
              QNAP NAS
            </a>,
            <a
              href="http://140.121.199.209:5000"
              target="_blank"
              className={classes.dropdownLink}
            >
              DBLab NAS
            </a>

          ]}
        />
      </ListItem>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          buttonText="實驗室論文系統"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={Book}
          dropdownList={[
            <a
              href="http://www.db.cse.ntou.edu.tw/DBLS/index.aspx"
              target="_blank"
              className={classes.dropdownLink}
            >
              DBLS
            </a>,
            <a
              href="http://www.db.cs.ntou.edu.tw:4219/dbls"
              target="_blank"
              className={classes.dropdownLink}
            >
              N-DBLS
            </a>

          ]}
        />
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to="/research" className={classes.navLink}>
          <Assignment className={classes.icons} /> Research
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link  to="/people" className={classes.navLink}>
          <People className={classes.icons} /> People
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to="/projects" className={classes.navLink}>
          <FolderSpecial className={classes.icons} /> Projects
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to="/links" className={classes.navLink}>
          <LinkSharp className={classes.icons} /> Links
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to="/honor" className={classes.navLink}>
          <School className={classes.icons} /> Honor Board
        </Link>
      </ListItem>
    </List>
  );
}

export default withStyles(headerLinksStyle)(HeaderLinks);
